import appConstants from "constants/app";
import { getTimetamp } from "helpers/time";
import { LocalStorage } from "helpers/webStorage";
import {
  mapForexRateResponse,
  T_ForexData,
  T_ForexResponse
} from "models/tools/forex";
import request, { T_HTTPMethod } from "helpers/request";

export async function fetchForexData() {
  let result: T_ForexData | null = JSON.parse(
    LocalStorage.getItem(appConstants.webStorage.forexData) ?? "null"
  );

  // Make the API call only when necessary
  if (
    !result || // if there is no cached data yet
    getTimetamp() > result.timeNextUpdate // // OR if cached data is expired
  ) {
    const response = await request<T_ForexResponse>({
      method: T_HTTPMethod.GET,
      endpoint: appConstants.API.forex
    });
    if (response.result) {
      result = mapForexRateResponse(response.result);
      // Update cache with latest data
      LocalStorage.setItem(
        appConstants.webStorage.forexData,
        JSON.stringify(result)
      );
    }
  }

  return result?.conversionRates;
}
